<template>
  <section class="clearfix">
    <div class="left">
      <el-card class="box-card">
        <div slot="header" class="clearfix header">
          <span>在线用户<i>点击小气泡进行聊天</i></span>
        </div>
        <div v-for="o in 4" :key="o" class="text item">
          <div class="col">
            <span>昵称</span>
            <i class="iconfont sort" @click="show = 1">&#xe618;</i>
            <em v-if="show == 1">Chating...</em>
          </div>
        </div>
      </el-card>
    </div>
    <div class="right">
      <div class="chatPanel">
        <header>
          Web聊天室  (昵称)
        </header>
        <article>

        </article>
        <textarea v-model="sendMessage"></textarea>
        <footer>
          <el-button class="btn">发送</el-button>
        </footer>
      </div>
    </div>
  </section>
</template>

<script>
// 页面为聊天室页面，websocket，选择聊天对象肯定是你关注的或者互相关注的
export default {
  name: "ChatMain",
  data() {
    return {
      show: 0,
      sendMessage:'',
      user:''   ,//这是本人
      users:[]  //这是聊天对象
    };
  },
  methods:{
    // 初始化连接websocket
    init(){
      // email
      this.user = this.$store.state.email;
      
      let _this = this;

      if (typeof (WebSocket) == "undefined") {
        alert("您的浏览器不支持WebSocket！");
      }else{
        console.log("支持WebSocket！");
        
        let socketUrl = 'ws://';

        if(socket != null){
          socket.close();
          socket = null;
        }
        // 开启了websocket服务！
        socket = new WebSocket(socketUrl);
        // 打开事件
        socket.onopen = function(){
          console.log('服务已打开!');
        }
        socket.onmessage = function(msg){
          console.log('收到数据!' + msg.data);

          _this.users = msg.data;//对json进行解析，主要看是什么格式

          // 过滤信息
          if (msg.data.users) {
            _this.users = msg.data.users.filters(user => user.email !== _this.user)
          }

        }



      }





    }
  }
};
</script>

<style lang='less'>
.left{
  float: left;
}
.right{
  float: left;
  .chatPanel{
    width: 482px;
    height: 600px;
    border: 1px solid #999;
    header{
      width: 100%;
      height: 7%;
      padding-top: 8px;
      text-align: center;
      border-bottom: 1px solid #666;
    }
    article{
      width: 100%;
      height: 60%;
      border-bottom: 1px solid #666;
    }
    textarea{
      width: 100%;
      height: 28%;
      margin: 0 !important;
      border: 0 !important;
      background-color: white;
    }
    footer{
      width: 100%;
      height: 5%;
      // padding-top: 5px;
      border-top: 1px solid #666;
      .btn{
        float: right;
        height: 90% !important;
        padding-top: 5px;
      }
    }
  }
}
.el-card__header {
  padding-top: 8px !important;
  padding-bottom: 8px !important;
  span {
    i {
      font-size: 12px;
    }
  }
}
.text {
  font-size: 14px;

  .col {
    display: block;
    span {
      font-size: 16px;
      user-select: none;
    }
    .sort {
      font-size: 18px;
      cursor: pointer;
      &:hover {
        color: rgb(120, 198, 42) !important;
      }
    }
    em {
      font-size: 12px;
    }
  }
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 230px;
}
</style>