<template>
  <!-- <div class="container"> -->
    <div class="middle">
      <el-row>
        <el-col :span="5">
          <LeftNav title="首页">
            <ul class="row">
              <li>        
                <router-link to="">
                  <span class="iconfont">&#xe7c2;<i>全部</i></span>
                </router-link>
              </li>
              <li>
                <router-link to="">
                  <span class="iconfont">&#xe6c8;<i>最新</i></span>
                </router-link>
              </li>
              <li>
                <router-link to="">
                  <span class="iconfont">&#xe601;<i>关注</i></span>
                </router-link>
              </li>
            </ul>
          </LeftNav>
        </el-col>
        <el-col :span="19" :offset="5">
          <!-- 发布微博组件 -->
          <PublishDynamic @publish='receiveData'></PublishDynamic>
          <!-- 轮播图组件 -->
          <ImgSlide></ImgSlide>
          <!-- 主要动态的组件 -->
          <DynamicMainSection :datap='datap' @requestData='requestData'></DynamicMainSection>
        </el-col>
      </el-row>
    </div>
  <!-- </div> -->
</template>

<script>
import LeftNav from '@/components/LeftNav'
import PublishDynamic from '@/components/PublishDynamic'
import ImgSlide from '@/components/ImgSlide'
import DynamicMainSection from '@/components/DynamicMainSection'
import axios from 'axios'


export default {
  name: "MiddleSection",
  data(){
    return{
      datap:[
      ],
      flag:1,
      count:0,
    }
  },
  components:{
    LeftNav,
    PublishDynamic,
    ImgSlide,
    DynamicMainSection,
  },
  methods:{
    requestData(count){

      // this.$store.commit('changeLoading',true);

        axios({
        url:'http://82.157.48.184:5482/dynamic',
        method:'GET',
        params:{
          page:count,
          // topic:
        }
      }).then(response=>{
        // if (response.data.length == 5) {
          this.datap.push(response.data.info1)
          this.datap.push(response.data.info2)
          this.datap.push(response.data.info3)
          this.datap.push(response.data.info4)
          this.datap.push(response.data.info5)
        // }else{
          // alert('没了！！！！')
        // }

        // this.$store.commit('changeLoading',false);

        this.flag = 1;

        // console.log('then');
        
      }).catch(()=>{

        // this.$store.commit('changeLoading',false);

        console.log('catch');

        alert('没有更多信息了')

        // this.flag = 1;
      })
    },
     touchScroll() {
      let offsetHeight = Math.max(
        document.body.scrollHeight,
        document.body.offsetHeight
      ); // 内容高度
      let clientHeight =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight ||
        0; //视窗高度
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        0; //滚动条滚动高度
      return offsetHeight - clientHeight - scrollTop;
    },
    receiveData(x,value){
      this.datap.unshift(value);
      this.$router.go(0);
    },
    changeComment(value,did){

      console.log(this.datap[1].comments);

      for (let i = 0; i < this.datap.length; i++) {

        if (this.datap[i].dynamic.did === did) {
          
          this.datap[i].comments.push(value)

          console.log(this.datap);

          break;

        }
      }
    },
  },
  mounted() {
    window.addEventListener(
      "scroll",
      () => {
        if (this.$store.state.httpFlag === true ) {
          
          if (this.touchScroll() < 300) {
          
            if (this.flag == 1) {

              this.flag = 0;

              this.requestData(this.count++);

            }else{
              
              console.log('等一等')

            }
          }else{

            return;

          }
        }
      },
      true
    );
    this.$store.commit('changeHttpFlag',true);
    
    this.x.$on('comment',this.changeComment)
  },
  activated(){
    this.$store.commit('changeHttpFlag',true);
  }
  

  
}
</script>

<style lang='less'>

.middle {
  // height: 2500px;
  margin-top: 70px;
  section {
    position: relative;
    width: 100%;
    height: auto;
    padding: 20px;
    background-color: white;
    margin-bottom: 10px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgb(207, 204, 204);
    p {
      // width: 100%;
      // height: auto;
      word-wrap: break-word;
      word-break: normal;
    }
    textarea {
      display: block;
      width: 90%;
      height: 90px;
      margin-left: 40px;
      padding: 10px;
      outline: 0;
      border: 1px solid transparent;
      background-color: rgb(240, 241, 245);
      border-radius: 10px;
      resize: none;

      &:focus {
        border: 1px solid rgb(245, 131, 0);
      }

    }
    .row{
      width: 90%;
      span{
          cursor: pointer !important;
          &:hover{
            color: orange;
          }
      }
    
    }
  }
}
